import {connect} from "react-redux";
import {follow, unfollow, setCurrentPage, toggleIsFollowing, requestUsers} from "../../redux/usersReducer";
import React from "react";
import Users from "./Users";
import Preloader from "../common/preloader/Preloader";
import {compose} from "redux";
import {
  getCurrentPage,
  getFollowingInProgress,
  getIsFetching, getPagePortionSize,
  getPageSize,
  getTotalUsersCount,
  getUsers
} from "../../redux/usersSelectors";


class UsersContainer extends React.Component {
  componentDidMount() {
    this.props.getUsers(this.props.currentPage, this.props.pageSize);
  }

  onPageChanged = (pageNumber) => {
    this.props.setCurrentPage(pageNumber);
    this.props.getUsers(pageNumber, this.props.pageSize);
  };

  render() {
    return <>
      {this.props.isFetching ? <Preloader/> : null}
      <Users
        onPageChanged={this.onPageChanged}
        totalUsersCount={this.props.totalUsersCount}
        pageSize={this.props.pageSize}
        currentPage={this.props.currentPage}
        pagePortionSize={this.props.pagePortionSize}
        users={this.props.users}
        follow={this.props.follow}
        unfollow={this.props.unfollow}
        isFollowing={this.props.isFollowing}
        setCurrentPage={this.props.setCurrentPage}
      />
    </>
  }
}

let mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    pagePortionSize: getPagePortionSize(state),
    totalUsersCount: getTotalUsersCount(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    isFollowing: getFollowingInProgress(state),
  }
};

export default compose(connect(mapStateToProps,
  {follow, unfollow, setCurrentPage, toggleIsFollowing, getUsers: requestUsers}))(UsersContainer)