import React from 'react';
import styles from "./users.module.css";
import {NavLink} from "react-router-dom";
import noUser from "../../assets/img/no-user.png";

const User = ({user, isFollowing, follow, unfollow}) => {
  return (
    <div className={styles.user} key={user.id}>
      <div className={styles.img__wrap}>
        <NavLink to={`/profile/${user.id}`}>
          <img src={user.photos.small !== null ? user.photos.small : noUser} alt={user.name}/>
        </NavLink>
        {user.followed
          ? <button disabled={isFollowing.some(id => id === user.id)}
                    onClick={() => {
                      unfollow(user.id)
                    }}
                    className={styles.button}>Unfollow</button>
          : <button disabled={isFollowing.some(id => id === user.id)}
                    onClick={() => {
                      follow(user.id)
                    }}
                    className={styles.button}>Follow</button>
        }
      </div>
      <div className={styles.info}>
        <div className={styles.text}>
          <div className={styles.name}>{user.name}</div>
          <div className={styles.status}>{user.status}</div>
        </div>
      </div>
    </div>
  );
};

export default User;